<template>
  <div class="test">
    <p v-text="msg"></p>
  </div>
</template>
<script>
  /*data是函数！真正的值需要放在return的返回值中*/
  export default{
    data(){
     return{
       msg:'I am from test.vue'
     }
    }
  }
</script>

<style>
.test p{color: #00f}
</style>


